.center__part {
	font-family: 'Nunito';
	font-weight: 700;
	padding: 20px 0 20px 20px;
}

.img_audi {
	width: 300px;
	border: 2px solid rgb(3, 236, 3);
	border-radius: 10px;
}
.name {
	margin: 20px 0 20px 20px;
	font-size: 30px;
}

.interes {
	margin-left: 20px;
	margin-bottom: 40px;
	display: flex;
}
.form_interes {
	margin-right: 10px;
}
.posts {
	margin-left: 20px;	
}
.groupForm {
	display: flex;
}
.post {
	padding: 15px;
	border-bottom: 1px solid rgb(24, 179, 57);
}
.btnAdd {
	border: 0;
	border-left: 3px solid rgb(24, 179, 57);
	background: greenyellow;
	border-top-right-radius: 5px;
	border-bottom-right-radius: 5px;
	padding: 6px;
	height: 34.4px;
}